'use client';

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { useLanguage } from "@/contexts/language-context";
import type { Language } from "@/contexts/language-context";
import { translations } from "@/lib/translations";

export default function SettingsPage() {
    const [avatarPreview, setAvatarPreview] = useState<string | null>(null);
    const { language, setLanguage, dir } = useLanguage();
    
    const t = translations[language];

    const handleAvatarChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        if (e.target.files && e.target.files[0]) {
            const file = e.target.files[0];
            const reader = new FileReader();
            reader.onloadend = () => {
                setAvatarPreview(reader.result as string);
            };
            reader.readAsDataURL(file);
        }
    };

    return (
        <div className="space-y-6" dir={dir}>
            <div>
                <h1 className="text-3xl font-bold font-headline">{t.settings.title}</h1>
                <p className="text-muted-foreground">{t.settings.description}</p>
            </div>

            <Card>
                <CardHeader>
                    <CardTitle>{t.settings.language.title}</CardTitle>
                    <CardDescription>{t.settings.language.description}</CardDescription>
                </CardHeader>
                <CardContent>
                    <Select value={language} onValueChange={(value) => setLanguage(value as Language)}>
                        <SelectTrigger className="w-[180px]">
                            <SelectValue placeholder="Select language" />
                        </SelectTrigger>
                        <SelectContent>
                            <SelectItem value="en">English</SelectItem>
                            <SelectItem value="ar">العربية</SelectItem>
                        </SelectContent>
                    </Select>
                </CardContent>
            </Card>
            
            <Card>
                <CardHeader>
                    <CardTitle>{t.settings.profile.title}</CardTitle>
                    <CardDescription>{t.settings.profile.description}</CardDescription>
                </CardHeader>
                <CardContent className="space-y-4">
                    <div className="flex items-center space-x-4 rtl:space-x-reverse">
                        <Avatar className="h-20 w-20">
                          <AvatarImage src={avatarPreview || "https://placehold.co/96x96.png"} alt="User" data-ai-hint="man suit" />
                          <AvatarFallback>AD</AvatarFallback>
                        </Avatar>
                        <div className="space-y-2 flex-1">
                            <Label htmlFor="avatar-upload">{t.settings.profile.changeAvatar}</Label>
                            <Input id="avatar-upload" type="file" accept="image/*" onChange={handleAvatarChange} />
                            <p className="text-xs text-muted-foreground">
                                {t.settings.profile.uploadAvatar}
                            </p>
                        </div>
                    </div>
                    <div className="space-y-2">
                        <Label htmlFor="name">{t.settings.profile.name}</Label>
                        <Input id="name" defaultValue="Admin" />
                    </div>
                    <div className="space-y-2">
                        <Label htmlFor="email">{t.settings.profile.email}</Label>
                        <Input id="email" type="email" defaultValue="admin@telepowerx.com" />
                    </div>
                </CardContent>
            </Card>

            <Card>
                <CardHeader>
                    <CardTitle>{t.settings.password.title}</CardTitle>
                    <CardDescription>{t.settings.password.description}</CardDescription>
                </CardHeader>
                <CardContent className="space-y-4">
                    <div className="space-y-2">
                        <Label htmlFor="current-password">{t.settings.password.currentPassword}</Label>
                        <Input id="current-password" type="password" />
                    </div>
                    <div className="space-y-2">
                        <Label htmlFor="new-password">{t.settings.password.newPassword}</Label>
                        <Input id="new-password" type="password" />
                    </div>
                </CardContent>
            </Card>
            
            <div className="flex justify-end">
                <Button>{t.settings.saveChanges}</Button>
            </div>
        </div>
    )
}
